@font-face {
  font-family: 'FZShuSong-Z01';
  src: local('FZShuSong-Z01') local('Songti Sc'),
       url('/fonts/FZSSK.woff2') format('woff2');
}
@font-face {
  font-family: 'FZKai-Z03';
  src: local('FZKai-Z03') local('Kaiti SC'),
       url('/fonts/FZKTK.woff2') format('woff2');
}
@font-face {
  font-family: 'Source Han Serif CN';
  src: local('Source Han Serif CN'), local('Source Han Serif SC'),
       local('Source Han Serif TC'), local('Source Han Serif TW'),
       local('Source Han Serif'), local('Noto Serif CJK SC'), local('Songti SC'), local('SimSong'),
       url('/fonts/SourceHanSerifCN-Regular.woff2') format('woff2');
}
@font-face {
  font-family: 'FandolKai';
  src: local('Kaiti'), local('Kaiti SC'), local('STKaiti'), local('楷体'), local('SimKai'), local('AR PL KaitiM GB'), local('DFKai-SB'), local('FandolKai'),
       url('/fonts/AR-PL-KaitiM-GB.woff2') format('woff2');
}
body {
  font-family: Palatino, 'Palatino Linotype', 'Palatino LT STD', 'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
}
.home blockquote, .cn blockquote, .kai, .cn em, .cn .side, .hash-note, .hide-notes .toggle-notes {
  font-family: 'Palatino Linotype', 'Book Antiqua', Palatino, 'FZKai-Z03',FandolKai, serif;
}
.title {
  font-family: Palatino, 'Palatino Linotype', 'Palatino LT STD', 'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
}
code {
  font-family: Consolas, Courier, 'Courier New', 'FZKai-Z03', FandolKai, monospace;
}
pre, code {
  font-size: .95em;
}
h1, h2, h3 {
  font-family: Palatino, 'Palatino Linotype', 'Palatino LT STD', 'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
}
.dropdown-menu,
nav#navbar-main li.nav-item {
  /*font-size: 16px;*/
  /*font-weight: bold;*/
  font-family: Palatino, 'Palatino Linotype', 'Palatino LT STD', 'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
}
footer {
  font-size: 0.6rem;   // smaller text
  color: #777;          // optional: lighter color
  text-align: center;   // optional
  margin-top: 2rem;     // optional spacing
}

footer p {
  margin: 0.5rem 0;
}
@media (max-width: 600px) {
  footer {
    text-align: center;
  }
}