@import "fonts";
@import "rouge-github";

/* =========================================================
   Base
   ========================================================= */

body {
  background-color: #fff;
  padding: 50px;
  font: 14px/1.5 Palatino, 'Palatino Linotype', 'Palatino LT STD',
        'Latin Modern Roman', 'FZShuSong-Z01', 'Source Han Serif CN', serif;
  color: #727272;
  font-weight: 400;
}

h1, h2, h3, h4, h5, h6 {
  color: #222;
  margin: 0 0 20px;
}

p, ul, ol, table, pre, dl { margin: 0 0 20px; }

h1, h2, h3 { line-height: 1.1; }
h1 { font-size: 28px; }
h2 { color: #393939; }
h3, h4, h5, h6 { color: #494949; }

a { color: #267CB9; text-decoration: none; }
a:hover, a:focus { color: #069; font-weight: bold; }

a small {
  font-size: 11px;
  color: #777;
  margin-top: -0.3em;
  display: block;
}

blockquote {
  border-left: 1px solid #e5e5e5;
  margin: 0;
  padding: 0 0 0 20px;
  font-style: italic;
}

code, pre {
  font-family: Monaco, Bitstream Vera Sans Mono, Lucida Console, Terminal, Consolas,
               Liberation Mono, DejaVu Sans Mono, Courier New, monospace;
  color: #333;
}

pre {
  padding: 8px 15px;
  background: #f8f8f8;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  overflow-x: auto;
}

table { width: 100%; border-collapse: collapse; }
th, td { text-align: left; padding: 5px 10px; border-bottom: 1px solid #e5e5e5; }
th, dt { color: #444; font-weight: 700; }
img { max-width: 100%; }

kbd {
  background-color: #fafbfc;
  border: 1px solid #c6cbd1;
  border-bottom-color: #959da5;
  border-radius: 3px;
  box-shadow: inset 0 -1px 0 #959da5;
  color: #444d56;
  display: inline-block;
  font-size: 11px;
  line-height: 10px;
  padding: 3px 5px;
  vertical-align: middle;
}

hr { border: 0; background: #e5e5e5; height: 1px; margin: 0 0 20px; }
small { font-size: 11px; }
strong { color: #222; font-weight: 700; }

/* =========================================================
   Desktop / Default layout (two-column)
   ========================================================= */

.wrapper {
  width: 1000px;
  margin: 0 auto;
}

header {
  width: 250px;
  float: left;
  position: fixed;
  -webkit-font-smoothing: subpixel-antialiased;
}

section {
  width: 650px;
  float: right;
  padding-bottom: 50px;
}

footer {
  width: 250px;
  float: left;
  position: fixed;
  bottom: 50px;
  -webkit-font-smoothing: subpixel-antialiased;
}

/* =========================================================
   Sidebar styling
   ========================================================= */

header .sidebar { text-align: center; }

header .avatar {
  display: block;
  margin: 0 auto 24px;
}

header .sidebar-cv {
  display: inline-block;
  margin: 12px 0 32px;
  font-size: 20px;
  font-weight: 500;
  color: #8b0000;
}

header .sidebar-section { margin-bottom: 34px; }

header .sidebar-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #222;
}

header .sidebar-text {
  font-size: 18px;
  line-height: 1.4;
  color: #333;
}

header .sidebar-email {
  display: inline-block;
  font-size: 18px;
  margin-top: 0;
  color: #8b0000;
  text-decoration: none;
}

header .sidebar-icons {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  gap: 22px;
  margin-bottom: 24px;
}

header .sidebar-icons a { color: #111; text-decoration: none; }
header .sidebar-icons a:hover { color: #8b0000; }
header .sidebar-icons svg { display: block; }

/* =========================================================
   Mobile title
   (Only used in phone portrait stacked layout)
   ========================================================= */

.mobile-title { display: none; }

/* =========================================================
   Two-column responsive: prevent horizontal scrolling on
   iPad portrait and phone landscape by making widths fluid.
   (Still two-column, just fluid.)
   ========================================================= */

@media screen and (max-width: 1100px) and (min-width: 601px) {

  .wrapper, div.wrapper {
    width: min(96vw, 1000px);
    margin: 0 auto;
  }

  header {
    width: clamp(220px, 28vw, 260px);
  }

  section {
    width: auto;
    float: none;
    margin-left: calc(clamp(220px, 28vw, 260px) + 40px);
    padding-bottom: 50px;
  }

  /* keep footer fixed on tablet/desktop-sized screens */
  footer {
    width: clamp(220px, 28vw, 260px);
  }
}

/* =========================================================
   Phone portrait ONLY (<=600px): stacked layout
   ========================================================= */

@media screen and (max-width: 600px) and (orientation: portrait) {

  body { padding: 15px; }

  .wrapper, div.wrapper {
    width: auto;
    margin: 0;
  }

  header, section, footer {
    float: none !important;
    position: static !important;
    width: auto !important;
  }

  header {
    display: flex !important;
    flex-direction: column !important;
    align-items: center !important;
  }

  .mobile-title {
    display: block !important;
    order: 1 !important;
    text-align: center !important;
    font-size: 32px !important;
    font-weight: 700 !important;
    color: #222 !important;
    margin: 10px 0 14px !important;
  }

  header .avatar {
    order: 2 !important;
    margin: 0 0 24px !important;
  }

  header .sidebar { order: 3 !important; }

  section {
    border: 1px solid #e5e5e5;
    border-width: 1px 0;
    padding: 20px 0;
    margin: 0 0 20px;
  }

  /* hide section H1 in stacked phone layout */
  section h1,
  section > h1:first-child {
    display: none !important;
  }

  footer { margin-top: 18px; }
}

/* =========================================================
   Phone landscape: still two-column, but footer must NOT
   overlap sidebar content (so make footer flow below content)
   ========================================================= */

@media screen and (orientation: landscape) and (max-width: 900px) {

  header {
    position: fixed !important;
    padding-top: 60px !important;
    top: 0 !important;               /* ✅ space from top */
    bottom: 0 !important;
    width: clamp(220px, 28vw, 260px) !important;
    overflow-y: auto !important;
    padding-bottom: 50px !important;
  }


  footer {
    position: static !important;
    float: none !important;
    width: auto !important;
    margin-left: calc(clamp(220px, 28vw, 260px) + 40px);
    margin-top: 24px !important;
    font-size: 6px !important;
  }
}
